.home {
    &-title {
        font-family: 'kugile' !important;
        font-size: rem(48);
    }
}

.home-header {
    position: relative;
    width: 100%;
    //height: 100vh;
    //max-height: 100vh;
    height: rem(1000);
    overflow: hidden;


    &__topbar {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: rem(95);

        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 rem(120);

        border-bottom: 1px solid rgb(10, 10, 10, 0.1);

        @media screen and (max-width: 760px) {
            padding: 0 rem(10);
        }

        img {
            aspect-ratio: 285/42.73;
            height: rem(42.73);
        }

        &__content {
            display: flex;
            flex-direction: column;
        }

        &__date {
            font-size: rem(20);
            font-weight: 600;
            line-height: 150%;
        }

        &__start {
            cursor: pointer;
            background: linear-gradient(135.00deg, rgb(102, 1, 22), rgb(199, 54, 89), rgb(169, 29, 58));
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
            text-fill-color: transparent;
            font-size: rem(20);
            font-weight: 600;
            line-height: 150%;
        }
    }

    &__content {
        position: absolute;
        left: rem(120);
        top: rem(150);

        display: flex;
        flex-direction: column;
        gap: rem(28);

        @media screen and (max-width: 560px) {
            width: 100%;
            padding-right: rem(20);
        }

        @media screen and (max-width: 1250px) {
            bottom: rem(80);
            left: rem(10);
            top: unset;
            gap: unset;
        }

        &__category {
            height: rem(24);
            padding: rem(12) rem(16);
            color: rgb(10, 10, 10);
            font-family: 'kugile' !important;
            font-size: rem(20);


            @media screen and (max-width: 760px) {
                height: unset;
            }
        }

        &__title_1 {
            height: rem(100);
            color: rgb(228, 9, 127);
            font-size: rem(100);
            font-family: 'kugile' !important;

            @media screen and (max-width: 760px) {
                font-size: rem(60);
                height: unset;
            }
        }

        &__title_2 {
            height: rem(100);
            color: rgb(7, 181, 170);
            font-family: 'kugile' !important;
            font-size: rem(100);

            @media screen and (max-width: 760px) {
                font-size: rem(60);
                height: unset;
            }
        }

        &__desc {
            width: rem(660);
            font-size: rem(18);
            font-weight: 600;
            opacity: 0.3;
            line-height: 150%;

            @media screen and (max-width: 560px) {
                width: 100%;
            }
        }

        &__separator {
            margin-top: rem(20);
            width: rem(575);
            border: 1px solid rgb(10, 10, 10);
            opacity: 0.1;

            @media screen and (max-width: 5600px) {
                width: 100%;
            }
        }

        &__button {
            cursor: pointer;
            display: flex;
            justify-content: center;
            align-items: center;

            margin-top: rem(50);
            width: rem(390);
            height: rem(77);
            background: #E4097F;
            border-radius: rem(12);

            color: $white;
            font-size: rem(24);
            font-weight: 600;
            transition: all 0.3s ease;

            @media screen and (max-width: 560px) {
                width: 100%;
            }

            &:hover {
                scale: 1.05;
            }
        }
    }

    &__elements {
        &__top {
            height: rem(50);
            position: absolute;
            top: 0;
            left: 0;
        }

        &__bottom {
            height: rem(100);
            position: absolute;
            bottom: 80px;
            left: 0;
        }
    }

    &__talk {
        position: absolute;
        top: rem(50);
        left: rem(-170);
        padding: rem(16);
        border-radius: rem(16) rem(16) 0 rem(16);
        background: rgba(228, 9, 127, 0.1);

        color: rgb(10, 10, 10, 0.5);
        font-size: rem(14);
        font-weight: 600;
        line-height: 150%;

        @media screen and (max-width: 1560px) {
            top: rem(20);
            left: rem(-30);
        }

        @media screen and (max-width: 560px) {
            width: fit-content;
            top: rem(-20);
            left: unset;
            right: rem(200);
        }
    }

    &__avatar {

        &__itself {
            position: absolute;
            right: 0;
            bottom: 0;
            aspect-ratio: 665/830;
            height: rem(830);

            @media screen and (max-width: 1250px) {
                right: rem(-150);
            }

            @media screen and (max-width: 560px) {
                top: rem(150);
            }

            img {

                @media screen and (max-width: 760px) {
                }

                @media screen and (max-width: 415px) {
                    height: unset;
                }
            }
        }

        &__bg {
            position: absolute;
            bottom: 100px;
            right: 0;
            height: calc(100% - 200px);
        }
    }

    &__separator {
        width: 100%;
        position: absolute;
        bottom: 0;
        transform: translateY(rem(10));

        svg {
            width: 100%;
            height: auto;
            aspect-ratio: 1440/100;
        }
    }

    &__gradient {
        position: absolute;
        bottom: 0;
        width: 100%;
        height: rem(100);
        background: linear-gradient(180.00deg, rgba(10, 10, 10, 0), rgb(10, 10, 10) 82.244%);
        opacity: 0.2;
    }
}

.home-info {
    display: flex;
    flex-direction: column;
    align-items: center;

    background: rgb(255, 230, 236);
    padding: rem(10) rem(120) rem(60) rem(120);

    @media screen and (max-width: 1250px){
        padding: rem(10) rem(10) rem(60) rem(10);
    }

    &__content {
        margin-top: rem(40);
        width: 100%;

        display: grid;
        justify-content: center;
        grid-template-columns: repeat(2, minmax(15rem, 1fr));
        grid-row-gap: rem(10);
        grid-column-gap: rem(70);

        @media screen and (max-width: 760px){
            display: flex;
            flex-direction: column;
        }
    }

    &__item {
        height: rem(120);
        display: flex;
        align-items: center;
        gap: rem(24);

        &__number {
            width: rem(90);
            font-family: $alumni;
            font-size: rem(100);
            font-weight: 400;
            opacity: 0.1;
        }

        &__text {
            font-size: rem(20);
            font-weight: 700;
            line-height: 140%;

            red {
                color: rgb(228, 9, 127)
            }
        }
    }
}

.home-questions {
    position: relative;
    display: flex;
    align-items: center;
    padding: rem(80) rem(124) rem(80) rem(112);
    gap: rem(160);

    @media screen and (max-width: 1250px) {
        padding: rem(80) rem(10);
    }

    @media screen and (max-width: 760px) {
        gap: rem(40);
        flex-direction: column;
    }

    &__gradient {
        /* Rectangle 3 */
        position: absolute;
        width: 628px;
        height: 647px;
        left: rem(-150);
        top: rem(-100);

        border-radius: 500px;
        filter: blur(500px);
        background: rgb(199, 54, 89);
        opacity: 0.2;

        z-index: -1;

        @media screen and (max-width: 760px) {
            width: 100%;
            height: 100%;
        }
    }

    &__left {
        display: flex;
        flex-direction: column;
        align-items: center;

        &__image {
            width: rem(379);
        }

        &__text {
            padding: rem(28) rem(33) rem(20) rem(33);
            background: #E4097F;
            border-radius: rem(24);

            font-family: 'kugile' !important;
            text-align: center;
            color: $white;
            font-size: rem(36);
            line-height: 110%;
        }
    }

    &__right {
        display: flex;
        flex-direction: column;
        gap: rem(22);
    }

    &__content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: rem(16);
    }

    &__item {
        cursor: pointer;
        padding: rem(16) rem(32);
        color: rgb(10, 10, 10);
        font-size: rem(24);
        font-weight: 500;
        line-height: 120%;

        backdrop-filter: blur(64px);
        background: rgba(0, 0, 0, 0.05);
        border-radius: rem(20);
        transition: all 0.3s ease;

        &:hover {
            scale: 1.04;
        }
    }
}

.home-deadline {
    position: relative;
    padding: rem(40) rem(120);
    background: #FFE6EC;

    display: flex;
    align-items: center;
    justify-content: space-between;

    @media screen and (max-width: 1250px) {
        padding: rem(40) rem(10);
    }

    @media screen and (max-width: 760px) {
        padding: rem(40) rem(10);
        flex-direction: column;
        gap: rem(10);
    }

    &__gradient {
        position: absolute;
        width: 422px;
        height: 435px;
        left: 0;
        top: rem(-35);

        border-radius: 500px;
        filter: blur(500px);
        background: rgb(199, 54, 89);
        opacity: 0.4;

        @media screen and (max-width: 760px) {
            width: 100%;
            height: 100%;
        }
    }

    &__content {
        cursor: pointer;
        display: flex;
        flex-direction: column;
        gap: rem(8);
        padding: rem(24) rem(40);
        background: $white;
        border-radius: rem(24);
        transition: all 0.3s ease;

        &:hover {
            scale: 1.05;
        }

        &__title {
            color: rgb(30, 30, 30);
            font-size: rem(24);
            font-weight: 500;
            line-height: 150%;
            opacity: 0.3;
        }

        &__description {
            color: rgb(30, 30, 30);
            font-size: rem(20);
            font-weight: 500;
            line-height: 150%;
        }
    }
}

.home-feedback {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: rem(80) 0;

    &__gradient_1 {
        position: absolute;
        left: 0;
        width: rem(422);
        height: rem(435);
        bottom: rem(175);
        border-radius: 500px;
        filter: blur(500px);
        background: rgb(199, 54, 89);
        opacity: 0.4;

        @media screen and (max-width: 760px) {
            width: 100%;
            height: 100%;
        }
    }

    &__gradient_2 {
        width: rem(422);
        height: rem(435);
        position: absolute;
        right: 0;
        bottom: rem(50);
        border-radius: 500px;
        filter: blur(500px);
        background: rgb(199, 54, 89);
        opacity: 0.3;
    }

    &__slider {
        width: 100%;
        margin-top: rem(50);
    }

    &__item {
        width: 100%;
        display: flex;
        justify-content: center;

        img {
            cursor: pointer;
            width: rem(275);
        }
    }
}

.home-footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: rem(30);

    &__content {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: rem(32) rem(120);

        border-top: 1px solid rgb(10, 10, 10, 0.1);
        border-bottom: 1px solid rgb(10, 10, 10, 0.1);

        @media screen and (max-width: 1250px) {
            padding: rem(32) rem(10);
        }

        @media screen and (max-width: 760px) {
            padding: rem(32) rem(10);
            flex-direction: column;
            gap: rem(20);
        }

        &__left {
            color: rgb(10, 10, 10);
            font-size: rem(16);
            font-weight: 500;
            line-height: 150%;
            opacity: 0.3;
        }

        &__right {
            display: flex;
            flex-direction: column;
            gap: rem(16);
            padding-right: rem(30);
        }
    }

    &__link {
        cursor: pointer;
        font-size: rem(16);
        font-weight: 500;
        line-height: 150%;

        &:hover {
            text-decoration: underline;
        }
    }

    &__social {
        display: flex;
        gap: rem(24);
        margin-top: rem(26);

        &__link {
            cursor: pointer;
            transition: all 0.3s ease;

            &:hover {
                transform: scale(1.1);
            }
        }
    }

    &__copyright {
        margin-top: rem(32);
        color: rgb(10, 10, 10);
        font-size: rem(16);
        font-weight: 500;
        line-height: 150%;
        opacity: 0.3;
    }
}