.row {
	margin-left: rem(-15);
	margin-right: rem(-15);

	&.no-gutters {
		margin-left: 0;
		margin-right: 0;

		& > .col,
		& > [class*="col-"] {
			padding-left: 0;
			padding-right: 0;
		}
	}

	&.no-parent {
		& > .col,
		& > [class*="col-"] {
			position: static;
		}
	}

	&.no-wrap {
		flex-wrap: nowrap;
	}
}

