.container-fluid {
	padding-left: rem(60);
	padding-right: rem(60);
}

.row,
.d-flex {
	.pull-left {
		margin-right: auto;
	}

	.pull-right {
		margin-left: auto;
	}
}

img {
	max-width: 100%;
	height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	font-weight: 400;
	margin: 0;
}

a {
	color: $black;
	text-decoration: none;
	transition: all 0.3s ease;

	&:hover,
	&:focus {
		color: $black;
		text-decoration: none;
		transition: all 0.3s ease;
	}
}

.main-employ {
	display: flex;
	flex-direction: column;
	padding: rem(20);

	a {
		margin-top: rem(5);
	}

	& > div {
		display: flex;
		flex-direction: column;
		padding-top: rem(20);
	}
}

.mb-2 {
	margin-bottom: 1rem;
}

.notification {
	&-circle {
		display: flex;
		align-items: center;
		justify-content: center;
		min-width: rem(24);
		min-height: rem(24);
		width: rem(24);
		height: rem(24);
		border-radius: 50%;
		font-size: rem(14);
		font-weight: 500;
		line-height: 22px;
		
		&__red {
			color: #fff;
			background: #FF0000;
		}

	}
}

